<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="box">
          <!-- <div class="compare-flex-tit">商品对比</div> -->
          <div class="compare-flex">
            <div class="compare-left">
              <div class="compare-info">
                <ul>
                  <li style="height:220px">商品名称</li>
                </ul>
                <ul>
                  <li style="height:33px;" class="brand">品牌</li>
                </ul>
                <ul>
                  <li style="height:500px;overflow:auto;">主要参数</li>
                </ul>
              </div>
            </div>
            <div class="compare-right">
              <div v-for="(item,index) of compareList" :key="index" class="compare-info">
                <ul>
                  <li style="height:220px;vertical-align: middle;">
                    <div class="compare-list">
                      <div class="goods-img" @click="imgPreview(item.picture?item.picture:defaultImg)">
                        <img :src="item.picture" :onerror="defaultImg">
                      </div>
                      <div class="goods-name">{{ item.name }}</div>
                      <div class="goods-price">
                        <span class="num">{{ item.price_now }}</span>元
                      </div>
                      <div>
                        <van-button type="info" round plain size="mini" @click="deleteComparision(item.e_comparision_id, item.id)">删除</van-button>
                        <van-button type="info" round plain size="mini" @click="addBuycar(item.id,item.dsbs)">加入购物车</van-button>
                      </div>
                    </div>
                  </li>
                </ul>
                <ul>
                  <li style="height:33px;" class="brand">{{ item.brand_name }}</li>
                </ul>
                <ul>
                  <li>
                    <div class="compare-list" v-html="item.sku_text" />
                  </li>
                </ul>
              </div>
              <!-- <div class="compare-info">
                <ul>
                  <li style="height:220px;vertical-align: middle;">
                    <div class="compare-list">
                      <div class="goods-img">
                        <img src="@/assets/mall-images/goods/goods2.png">
                      </div>
                      <div class="goods-name">小米 红米Note8 pro 手机【现货速发/豪礼套装】</div>
                      <div class="goods-price">
                        <span class="num">1999</span>元
                      </div>
                    </div>
                  </li>
                </ul>
                <ul>
                  <li>小米</li>
                </ul>
                <ul>
                  <li style="height:500px;overflow:auto;">--</li>
                </ul>
              </div>
              <div class="compare-info">
                <ul>
                  <li style="height:220px;vertical-align: middle;">
                    <div class="compare-list">
                      <div class="goods-img">
                        <img src="@/assets/mall-images/goods/goods2.png">
                      </div>
                      <div class="goods-name">小米 红米Note8 pro 手机【现货速发/豪礼套装】</div>
                      <div class="goods-price">
                        <span class="num">1999</span>元
                      </div>
                    </div>
                  </li>
                </ul>
                <ul>
                  <li>小米</li>
                </ul>
                <ul>
                  <li>--</li>
                </ul>
              </div> -->
              <div v-if="compareList.length<4" class="compare-info">
                <ul>
                  <li style="height:220px;vertical-align: middle;">
                    <div class="compare-add" @click="goGooodsList()">
                      <i class="iconfont iconicon-add" />
                      请添加
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 空白区域，撑高度 -->
    <div class="common-space" />
    <div v-if="compareList.length>0" class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="qk()">清空对比商品</van-button></li>
      </ul>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { ImagePreview } from 'vant'
import { addCarts, deleteComparision, deleteAllComparision, listComparisionDetail } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'GoodsCompare',
  components: {
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      compareList: [],
      headerData: {
        title: '商品对比',
        leftUrl: 'back',
        leftText: ' '
      },
      address: {}
    }
  },
  created() {
    this.address = this.$route.params.address
    this.getlistComparisionDetail()
  },
  methods: {
    qk() {
      const arr = []
      for (let i = 0; i < this.compareList.length; i++) {
        arr.push(this.compareList[i].e_comparision_id)
      }
      deleteAllComparision.data = arr
      this.asyncApiModule(deleteAllComparision).then(res => {
        this.compareList = []
        this.pageStyles = 1
        this.$store.commit('goods/SET_DBIDS', [])
        this.$toast.success('清空对比商品成功！')
      }).catch(() => {
      })
    },
    // 删除对比商品
    deleteComparision(e_comparision_id, product_id) {
      deleteComparision.data = { e_comparision_id: e_comparision_id }
      this.asyncApiModule(deleteComparision).then(res => {
        this.$toast.success('删除成功')
        this.getlistComparisionDetail()
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 加入购物车
    async addBuycar(product_id, dsbs) {
      if (this.compareList.length < 3) {
        this.$toast.fail('对比商品数量不能少于三件！')
        return
      }
      var productInfo = {
        province: this.address.province,
        city: this.address.city,
        county: this.address.county,
        town: this.address.town,
        produceList: [],
        dsbs: dsbs
      }
      productInfo.produceList.push({
        skuId: product_id,
        num: 1
      })
      let state = true
      await this.$store.dispatch('goods/getLessStockAction', productInfo).then(res => {
        for (var key in res) {
          if (key !== 'success') {
            this.$toast(res[key])
            state = false
          }
        }
      }).catch(() => {
        this.loading = false
      })
      if (!state) {
        return
      }
      addCarts.data = { pcount: 1, product_id: product_id }
      this.asyncApiModule(addCarts).then(res => {
        this.$store.commit('goods/SET_DBIDS', [])
        // this.getlistComparisionDetail()
        this.$toast.success('加入购物车成功')
        this.$router.back()
      })
        .catch(() => {
        })
    },
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    // 获取对比商品列表
    getlistComparisionDetail() {
      this.asyncApiModule(listComparisionDetail).then(res => {
        this.compareList = res
      })
    },
    goAddresslist() {
      this.$router.push({
        name: 'AddressList'
      })
    },
    showPopup() {
      this.show = true
    },
    ConfirmOrder() {
      this.$dialog.confirm({
        title: '提示',
        message: '确认提交订单吗？'
      }).then(() => {
        // this.$router.push('/login')
        this.$toast.success('订单提交成功')
      }).catch(() => {
        // on cancel
        this.$toast.fail('订单已取消')
      })
    },
    DeleteSp() {
      this.$toast.success('删除成功')
    },
    goGooodsList() {
      this.$router.push('goodsList')
    }
  }

}
</script>

